import { Component, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';

//router

import {Router} from '@angular/router';

//services

import {ValidateService} from '../../services/validate.service';

//flash messages service

import { FlashMessagesService } from 'angular2-flash-messages';

//auth service

import {AuthService} from '../../services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name:String;
  username:String;
  email:String;
  password:String;

  constructor(private validservice:ValidateService,
              private _flashMessagesService: FlashMessagesService,
              private _AuthService:AuthService,
              private _Router:Router) { }

  ngOnInit() {
  }

  onRegisterSubmit(){

    const  user = {

      name:this.name,
      username:this.username,
      email:this.email,
      password:this.password


    };

    //required fields

    if(!this.validservice.validateRegister(user))
    {

      console.log("please fill in all field!");

      this._flashMessagesService.show('please fill in all field!', { cssClass: 'alert-danger', timeout: 3000 });

      return false;

    }

    if(!this.validservice.validateEmail(user.email))
    {

      console.log("please enter a valid email!");

      this._flashMessagesService.show('please enter a valid email!', { cssClass: 'alert-danger', timeout: 3000 });

      return false;

    }

    //registration process

    this._AuthService.registerUser(user).subscribe(data=>{

        if(data.success){

          this._flashMessagesService.show('You are now registered!', { cssClass: 'alert-success', timeout: 3000 });
  
          this._Router.navigate(['/login']);

          console.log("login navigate");
        }else{

          this._flashMessagesService.show('Something went wrong!', { cssClass: 'alert-danger', timeout: 3000 });
  
          this._Router.navigate(['/register']);

          console.log("register navigate");

        }

    });

  }


}
