import { Component, OnInit } from '@angular/core';
import { tokenKey } from '../../../../node_modules/@angular/core/src/view';

//auth service

import {AuthService} from '../../services/auth.service';

//flash messages service

import { FlashMessagesService } from 'angular2-flash-messages';

//router

import {Router} from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  username:String;
  password:String;

  constructor( private _flashMessagesService: FlashMessagesService,
               private _AuthService:AuthService,
               private _Router:Router) { }

  ngOnInit() {
  }

  onLoginSubmit(){

      const user = {

          username:this.username,
          password:this.password

      };

  //check login

  this._AuthService.authenticateUser(user).subscribe(data=>{

      if(data.success){

        this._AuthService.storeUserData(data.token,data.user);

        this._flashMessagesService.show('You are now logged in!', { cssClass: 'alert-success', timeout: 3000 });

        this._Router.navigate(['/dashboard']);

      }else{

        this._flashMessagesService.show(data.msg, { cssClass: 'alert-danger', timeout: 3000 });

        this._Router.navigate(['/login']);

      }

  });


  }

}
